import HaiAngWork from "./HaiAngWork";
import './BoatWork.css';

class BoatWork extends HaiAngWork {

    getLockedMechanism = () => {
       return (
           <div id="boatwork_unopen_box" onClick={()=>{this.props.onTalk("箱子", "（蓋子動也不動，就是個箱子，好像缺少了什麼？）")}}>
               <div id="key_hole"></div>
           </div>
       ) 
    }

    getUnlockedMechanism = () => {
        return (
            <div id="boatwork_open_box">
                <div id="key_hole"></div>
            </div>
        ) 
    }

    onDivMouseDown = (e) => {
        if (this.isGotFish(e)) {
            this.props.onTalk("你", "(抓到一隻魚了)");
            this.props.onWin(this.getWork().id);
        } else
            this.props.onStartConversation();
    }

    isGotFish = (e) => {
        return this.isOnFishman(e) && this.isFishAroundRod()
    }

    isFishAroundRod = () => {
        const frame = this.spritesheeInstance.getInfo("frame");
        return this.isBetween(frame, 143, 146) || this.isBetween(frame, 167, 169);
    }

    isOnFishman = (e) => {
        let target = e.currentTarget.getBoundingClientRect();
        const x = (e.clientX - target.left) / target.width * 100;
        const y = (e.clientY - target.top) / target.height * 100;
        return this.isBetween(x, 10, 24) && this.isBetween(y, 60, 70);
    }

    isBetween = (target, min, max) => {
        return (target >= min && target <= max);
    }
}

export default BoatWork;