
import HaiAngWork from "./HaiAngWork"
import BoatWork from "./BoatWork";
import CanbeerFrog from "./CanBeerFrog";
import WalkingCat from "./WalkingCat";
import BearsCoffee from "./BearsCoffee";
import WindowAndCat from "./WindowAndCat";
import { worksById } from '../GameConstants';

class HaiAngWorkFactory {

    static build(id, isWin, onTalk, onWin, onStartConversation) {
        const work = worksById[id]

        // if (isWin)
            // return <WinWork />

        switch (id) {
            case worksById.CATBOAT.id:
                return <BoatWork key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
            case worksById.CANBEER_FROG.id:
                return <CanbeerFrog key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
            case worksById.BEARS_COFFEE.id:
                return <BearsCoffee key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
            case worksById.WALKING_CAT.id:
                return <WalkingCat key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
            case worksById.WINDOW_AND_CAT.id:
                return <WindowAndCat key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
            default:
                return <HaiAngWork key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} onStartConversation={onStartConversation} />
        }
    }
}

export default HaiAngWorkFactory;