import React from 'react';
import Spritesheet from 'react-responsive-spritesheet';
import './HaiAngWork.css';
import SceneBox from '../ui/SceneBox';


class HaiAngWork extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            conversationIndex: -1,
            isEnd: this.props.isWin,
            isMechanismShow: false
        };
    }

    render() {
        const work = this.getWork();
        return (
            <div className="haiang_workplace">
                <div className={this.getSparkleClassName("frame")}
                    onMouseDown={this.onDivMouseDown}>
                    <Spritesheet
                        className="haiang_work"
                        style={{ width: "450px" }}
                        image={work.src}
                        startAt={work.start + 1}
                        endAt={work.start + work.steps}
                        direction={`forward`}
                        widthFrame={125}
                        heightFrame={125}
                        steps={work.steps}
                        fps={work.fps}
                        timeout={0}
                        loop
                        isResponsive
                        getInstance={spritesheet => {
                            this.spritesheeInstance = spritesheet;
                        }}
                    />
                </div>
                <div className="introduction_box">
                    <p className="intro_box_author">HAI3-ANG 2020</p>
                    <p>#####</p>
                    <p>^^^^^^^^<br />^^^^^^^^<br />^^^^^^^^^</p>
                    {work.name}
                </div>
                <div
                    className={this.getSparkleClassName("work_mechanism")}
                    onClick={() => { this.setState({ isMechanismShow: true }) }} />
                {this.getMechanismModal()}
            </div>
        );
    }

    getWork = () => {
        return this.props.data;
    }

    onDivMouseDown = (e) => {
        this.props.onStartConversation();
    }


    getSparkleClassName = (className) => {
        if (!this.props.isWin)
            return `${className} sparkle`;
        else
            return className;
    }

    getMechanismModal = () => {
        if (this.state.isMechanismShow)
            return (
                <SceneBox onClickClose={this.onClickMechanismClose}>
                    {this.getMessageCard()}
                    {this.getMechanism()}
                </SceneBox>
            )
    }

    getMessageCard = () => {
        if (!this.props.isWin)
            return (
                <div className="mechanism_card">
                    無料請自取～<br />
                    （ouo/ 印章在裡面↓↓↓）
                </div>
            );
        else
            return (
                <a target="_blank" rel="noopener noreferrer" href={this.getRewardHref()}>
                    <button className="work_reward_btn">
                        拿走<br />小表符
                    </button>
                </a>
            )
    }

    getRewardHref = () => {
        return this.props.data.rewardHref;
    }

    getRewardButton = () => {

    }

    getMechanism = () => {
        if (this.props.isWin)
            return this.getUnlockedMechanism();
        else
            return this.getLockedMechanism();
    }

    getUnlockedMechanism = () => {
    }

    getLockedMechanism = () => {
    }


    onClickMechanismClose = () => {
        this.setState({ isMechanismShow: false });
    }
}

export default HaiAngWork;