import React from 'react';
import './Box.css';


class Box extends React.Component {
    render() {
        return (
            <div id={this.props.id} className="haiang_box" onClick={this.props.onClick}>
                <button className="haiang_box_btn_close" onClick={this.props.onClickClose} />
                {this.props.children}
            </div>
        );
    }
}

export default Box;