
import HaiAngWork from "./HaiAngWork";
import { PlayButton, Timer } from 'react-soundplayer/components';
import { withCustomAudio } from 'react-soundplayer/addons';
import whealeSound from '../../sound/52hz.mp3';
import './WindowAndCat.css';

class WindowAndCat extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, hertz: 250 };
    }

    getLockedMechanism = () => {
        return (
            <div id="windowandcat_unopen_box">
                <div id="whale_sound_radio">
                    <SoundPlayer
                        streamUrl={whealeSound}
                        preloadType="auto" />
                </div>
                <h1 id="hertz">{this.state.hertz}Hz</h1>
                <input id="windowandcat_hertz_slide"
                    orient="vertical"
                    type="range"
                    defaultValue={250}
                    min="36" max="276" step="4" onChange={(e) => { this.setState({ hertz: e.target.value }) }} />
                <button id="windowandcat_open_box_button" onClick={this.inputAnswer}>打開箱子</button>
            </div>
        )
    }


    getUnlockedMechanism = () => {
        return (
            <div id="windowandcat_open_box">
                <div id="whale_sound_radio">
                    <SoundPlayer
                        streamUrl={whealeSound}
                        preloadType="auto" />
                </div>
                <input
                    readOnly
                    id="windowandcat_hertz_slide"
                    orient="vertical"
                    type="range"
                    defaultValue={52}
                    min="36" max="276" step="4" />
            </div>
        )
    }

    inputAnswer = () => {
        if (this.state.hertz === "52") {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("機關", "(打開了)");
        } else
            this.props.onTalk("機關", "(喀答一聲，毫無反應)");

    }
}


const SoundPlayer = withCustomAudio(props => {
    return (
        <div>
            <PlayButton {...props} />
            <Timer {...props} />
        </div>
    );
});

export default WindowAndCat;