import './Game.css';
import React from 'react';
import HaiAngWorkFactory from './works/HaiAngWorkFactory';
import { orderedWorks, decorateWorks } from './GameConstants';
import DialogBox from './ui/DialogBox';
import SceneBox from './ui/SceneBox';
import Modal from './ui/Modal';
import DecorateWork from './ui/DecorateWork';

class Game extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            workIndex: 0,
            message: "",
            messageCount: 0,
            win: [],
            isDialogShown: false,
            isPassRewardShown: false,
            isGameIntroShown: true
        };
    }

    render() {
        const work = this.getWork(this.state.workIndex);
        return (
            <div id="game">
                {this.getMessage()}
                {this.getPassReward()}
                <div id="curtain" key={this.state.workIndex} />
                {this.getTicket()}
                {this.getGameIntro()}
                <div id="gallery">
                    <DecorateWork src={this.getLeftDecorateWork()} />
                    {this.getPreviousButton()}
                    {HaiAngWorkFactory.build(work.id, this.isWin(work.id), this.onTalk, this.onWin, this.onStartConversation)}
                    {this.getNextButton()}
                    <DecorateWork src={this.getRightDecorateWork()} />
                </div>
                {this.getConversation()}
                <div id="floor"></div>
            </div>
        )
    }

    getIntroBoard = () => {
        return (
            <div className="intro_board">
                ####<br /><br />
                ^^^^^<br />
                ^^^^^<br />
            </div>
        );
    }

    getLeftDecorateWork = () => {
        return this.getDecorateWorkOfIndex(this.state.workIndex * 2);
    }

    getRightDecorateWork = () => {
        return this.getDecorateWorkOfIndex(this.state.workIndex * 2 + 1);
    }

    getDecorateWorkOfIndex = (decorateWorkIndex) => {
        if (this.isDecorateWorkExist(decorateWorkIndex)) {
            return decorateWorks[decorateWorkIndex]
        }
    }

    isDecorateWorkExist = (decorateWorkIndex) => {
        return decorateWorks.length > decorateWorkIndex;
    }

    onTalk = (who, content) => {
        this.setState({
            message: `${who}: ${content}`,
            messageCount: this.state.messageCount + 1
        });
    }

    getMessage = () => {
        if (this.state.message.length !== 0)
            return (
                <div id="game_message" key={`${this.state.message}_${this.state.messageCount}`}>
                    <h3>{this.state.message}</h3>
                </div>
            )
    }

    getGameIntro = () => {
        if (this.state.isGameIntroShown) {
            return (
                <Modal id="game_intro_modal">
                    <div id="game_intro">
                        <p>歡迎光臨海翁的藝廊</p>
                        <p>左上角是您的<mark>入場券</mark></p>
                        <p><mark>每個展間</mark>都有小表符可以領取</p>
                        <p>當您參觀完各個展間後，會幫您<mark>蓋個章</mark></p>
                        <p>如果<mark>集滿五個章</mark>就可以在櫃台兌換一份小禮物喔~</p>
                        <p>任何展間都可以自由參觀</p>
                        <p>本藝廊的<mark>畫作也都可以自由觸摸</mark></p>
                        <p><mark>請放鬆心情，慢慢欣賞，謝謝您。</mark></p>
                        <button onClick={this.onClickEnterGallary}>進入藝廊</button>
                    </div>
                </Modal>
            )
        }
    }


    getPassReward = () => {
        if (this.state.isPassRewardShown)
            return (
                <Modal id="game_intro_modal">
                    <div id="game_intro">
                        <p>（你來到櫃檯前）</p>
                        <p>感謝您參觀完本藝廊，並集滿五個印章</p>
                        <p>不曉得您今天過得如何呢？</p>
                        <p>希望能帶給您放鬆又有趣的體驗。</p>
                        <p>這裡是您的小禮物，感謝參觀，祝您有愉快的一天！</p>
                        <a href="https://www.plurk.com/p/n2dhmw" target="_blank" rel="noopener noreferrer" > <button>兌換小禮物!</button> </a>
                        <button onClick={this.onClickPassRewardClose}>回到藝廊</button>
                    </div>
                </Modal>
            )
    }

    onClickEnterGallary = () => {
        this.setState({ isGameIntroShown: false });
    }

    getTicket = () => {
        let className = "";
        let onClickTicket = () => {};
        if (this.isAllPass()) {
            className = "ticket_reward_blink";
            onClickTicket = this.showPassReward;
        }
        return (
            <div id="ticket" className={className} onClick={onClickTicket}>
                <ul>
                    {orderedWorks.map(({ id }) => { return this.getTicketGrid(this.isWin(id)) })}
                </ul>
            </div>
        );
    }

    showPassReward = () => {
        if (!this.state.isDialogShown) this.setState({ isPassRewardShown: true });
    }

    isAllPass() {
        return (this.state.win.length === 5);
    }

    onClickPassRewardClose = () => {
        this.setState({ isPassRewardShown: false });
    }

    getTicketGrid = (isWin) => {
        if (isWin)
            return (<li className="ticket_grid stamp"></li>);
        else
            return (<li className="ticket_grid"></li>);
    }

    getWork(index) {
        return orderedWorks[index];
    }

    getPreviousButton = () => {
        if (this.state.workIndex > 0)
            return (
                <div className="gallery_wall">
                    <button id="gallery_previous_room" onClick={() => this.onClickPrevious()}></button>
                </div>
            );
        else
            return this.getHiddenButton();
    }


    onClickPrevious() {
        const index = this.state.workIndex;
        if (index > 0)
            this.setState({ workIndex: index - 1, message: "" });
    }

    isWin = (id) => {
        return (this.state.win.includes(id))
    }

    onWin = (id) => {
        this.setState({ win: [...this.state.win, id] });
    }

    getNextButton = () => {
        if (this.isLastWork(this.state.workIndex))
            return (
                <div className="gallery_wall">
                    <button id="gallery_next_room" onClick={() => this.onClickNext()}></button>
                </div>
            );
        else
            return this.getHiddenButton();
    }

    getHiddenButton = () => {
        return (<div class="gallery_wall"></div>);
    }

    isLastWork(index) {
        return index < orderedWorks.length - 1
    }

    onClickNext() {
        const index = this.state.workIndex;
        if (this.isLastWork(index))
            this.setState({ workIndex: index + 1, message: "" });
    }

    getConversation = () => {
        console.log("con", this.state.isDialoShown);
        if (this.state.isDialogShown) {
            const conversationScript = this.getWork(this.state.workIndex).conversation;
            return (
                <DialogBox onClose={this.onDialogClose} dialogScript={conversationScript} />
            )
        }
    }

    onDialogClose = () => {
        this.setState({ isDialogShown: false });
    }

    onStartConversation = () => {
        this.setState({ isDialogShown: true });
    }



}

export default Game;