import './SceneBox.css';
import React from 'react';
import Modal from './Modal';
import Box from './Box';

class SceneBox extends React.Component {
    render() {
        return (
            <Modal id="mechanism_modal">
                <Box id="mechanism_box" onClickClose={this.props.onClickClose}>
                    {this.props.children}
                </Box>
            </Modal>
        )
    }
}

export default SceneBox;