import HaiAngWork from "./HaiAngWork";
import './CanBeerFrog.css';
import redTape from '../../img/sprite/tape_red.png';
import greenTape from '../../img/sprite/tape_green.png';
import purpleTape from '../../img/sprite/tape_purple.png';
import whiteTape from '../../img/sprite/tape_white.png';

class CanbeerFrog extends HaiAngWork {

    constructor(props) {
        super(props);
        this.state = { ...this.state, colors: [] };
    }

    getLockedMechanism = () => {
        return (
            <div id="canbeerfrog_unoopen_box">
                <div id="canbeerfrog_answer">
                    {this.getInputColors()}
                </div>

                <ul id="tape_btn_content">
                    {["R", "G", "W", "P"]
                        .map((v) => { return this.getButton(v) })}
                </ul>
                <button id="canbeerfrog_open_btn" onClick={this.onEnter}>打開箱子</button>
            </div>
        )
    }

    getUnlockedMechanism = () => {
        return (
            <div id="canbeerfrog_oopen_box">
                <ul id="tape_btn_content">
                    {["R", "G", "W", "P"]
                        .map((v) => { return this.getButton(v) })}
                </ul>
            </div>
        )
    }

    getInputColors = () => {
        return this.state.colors.map((color) => { return this.getColorImage(color) });
    }

    getColorImage = (color) => {
        switch (color) {
            case "R":
                return (<i id="red_note">♩</i>);
            case "G":
                return (<i id="green_note">♩</i>);
            case "W":
                return (<i id="white_note">♩</i>);
            case "P":
                return (<i id="purple_note">♩</i>);
        }
    }


    getButton = (answer) => {
        return (
            <li>
                <button onClick={() => this.inputAnswer(answer)}>
                    <img src={{ "R": redTape, "G": greenTape, "W": whiteTape, "P": purpleTape }[answer]} />
                </button>
            </li>
        )
    }

    getAnswer = () => {
        let answer = "";
        for (let i in this.state.colors)
            answer += ` ${this.state.colors[i]}`;
        return answer;
    }

    inputAnswer = (color) => {
        if (this.state.colors.length < 8) {
            const newColors = [...this.state.colors, color];
            this.setState({ colors: newColors });
        }
    }

    onEnter = () => {
        if (this.isRightAnswer(this.state.colors)) {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("鎖", "(打開了)");
        } else {
            this.props.onTalk("鎖", "(喀答一聲，毫無反應)");
            this.setState({ colors: [] });
        }
    }

    isRightAnswer = (colors) => {
        return ["G", "R", "G", "R", "P"]
            .every((v, i) => v === colors[i]);
    }
}

export default CanbeerFrog;