import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';
import './WalkingCat.css';
import HaiAngWork from "./HaiAngWork";
import mail from '../../img/sprite/mail_open.png';
import unopenMail from '../../img/sprite/mail_unopen.png';

class WalkingCat extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, code: "", isLetterOpen: false };
    }

    getLockedMechanism = () => {
        return (
            <div id="walkingcat_unopen_box">
                <div onClick={(e) => {
                    if (!this.state.isLetterOpen) {
                        this.props.onTalk("你", "(打開了一封有密碼的信)");
                        this.setState({ isLetterOpen: true })
                    }
                }}>
                    <div id="walking_cat_mail">
                        {this.getLetter()}
                    </div>
                </div>
                <input
                    id="walking_cat_answer_input"
                    className="partitioned"
                    onChange={(e) => { this.setState({ code: e.target.value }) }}
                    disabled
                    maxLength={3}
                    value={this.state.code}
                />
                <div id="walkingcat_keyboard">
                    {this.getKeyboard()}
                    <button onClick={this.inputAnswer}>打開箱子</button>
                </div>
            </div>
        )
    }

    getUnlockedMechanism = () => {
        return (
            <div id="walkingcat_open_box">
                <div>
                    <div id="walking_cat_mail">
                        <Zoom>
                            <img id="walking_cats_mail_open" src={mail} />
                        </Zoom>
                    </div>
                </div>
                <div id="walkingcat_keyboard">
                    {this.getKeyboard()}
                    <button style={{ visibility: "hidden" }}>打開箱子</button>
                </div>
            </div>
        )
    }

    getKeyboard = () => {
        return ["Z", "X", "M", "E", "G", "S", "W", "N", "I", "A", "H", "Y"].map((v) => { return this.getButton(v) })
    }

    getButton = (v) => {
        return (<button onClick={() => { this.onClickKey(v) }}>{v}</button>);
    }

    onClickKey = (v) => {
        if (this.state.code.length < 3)
            this.setState({ code: this.state.code + v })
    }

    getLetter = () => {
        if (!this.state.isLetterOpen) {
            return (
                <img id="walking_cats_mail_unopen" src={unopenMail} />
            )
        } else {
            return (
                <Zoom>
                    <img id="walking_cats_mail_open" src={mail} />
                </Zoom>
            )
        }
    }

    inputAnswer = () => {
        console.log(this.state.code.toLowerCase)
        if (this.state.code.toLowerCase() === "sea") {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("機關", "(打開了)");
        } else {
            this.props.onTalk("機關", "(喀答一聲，毫無反應)");
            this.setState({ code: "" });
        }
    }
}

export default WalkingCat;