import React from 'react';
import './DialogBox.css';
import Modal from './Modal';
import Box from './Box';
import Typist from 'react-typist';


class DialogBox extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            index: 0,
        };
    }

    render() {
        let { who, content } = this.props.dialogScript[this.state.index];
        return (
            <Modal id="dialog_modal">
                <Box id="dialog_box" onClick={this.onClickNext} onClickClose={this.onClickBoxClose}>
                    <p>
                        <span id="talker_name">{who}</span>
                        <Typist
                            key={this.state.index}
                            cursor={{ show: false, }}>
                            <span className="conversarion_content">
                                {content}
                            </span>
                        </Typist>
                    </p>
                    {(this.isLastDialog()) ? "" : <button className="btn_next" alt="NEXT" />}
                </Box>
            </Modal >
        )
    }

    onClickBoxClose = (e) => {
        e.stopPropagation();
        this.props.onClose();
    }

    onClickNext = () => {
        if (this.isLastDialog())
            this.props.onClose();
        else
            this.setState({ index: this.state.index + 1 });
    }

    isLastDialog = () => {
        return this.state.index >= (this.props.dialogScript.length - 1);
    }

}


export default DialogBox;