import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';
import './BearsCoffee.css';

import HaiAngWork from "./HaiAngWork";
import bearsPhotos from '../../img/sprite/coffee_photos.png';


class BearsCoffee extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, hours: 0, minutes: 0 };
    }

    getLockedMechanism = () => {
        return (
            <div id="bears_unopen_box">
                <div id="bears_photos_content">
                    <Zoom>
                        <img id="bears_photos" src={bearsPhotos} />
                    </Zoom>
                </div>
                <div id="bears_timer">
                    <input defaultValue={6} type="number" min={0} max={23}
                        onChange={(e) => { this.setState({ hours: e.target.value }) }} />
                    ：
                    <input defaultValue={15} type="number" min={0} max={45} step={15}
                        onChange={(e) => { this.setState({ minutes: e.target.value }) }} />
                    <button onClick={this.inputAnswer}>打開箱子</button>
                </div>
            </div>
        )
    }
    getUnlockedMechanism = () => {
        return (
            <div id="bears_open_box">
                <div id="bears_photos_content">
                    <Zoom>
                        <img id="bears_photos" src={bearsPhotos} />
                    </Zoom>
                </div>
                <div id="bears_timer">
                    <input defaultValue={16} disabled onKeyDown={(e) => { e.preventDefault(); return false; }} />
                    ：
                    <input defaultValue={0} disabled onKeyDown={(e) => { e.preventDefault(); return false; }} />
                    <button style={{ visibility: "hidden" }}>打開箱子</button>
                </div>
            </div>
        )
    }

    inputAnswer = () => {
        if (this.state.hours === "16" && (this.state.minutes === "0" || this.state.minutes === "00")) {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("時鐘", "(打開了)");
        } else
            this.props.onTalk("時鐘", "(喀答一聲，毫無反應)");
    }
}

export default BearsCoffee;
