import React from 'react';


class DecorateWork extends React.Component {
    render() {
        return (
            <div className="gallery_wall">
                <div className="left_decorate_work">
                    <img src={this.props.src} />
                </div>
                {this.getIntroBoard()}
            </div>
        );
    }

    getIntroBoard = () => {
        return (
            <div className="intro_board">
                ####<br /><br />
                ^^^^^<br />
                ^^^^^<br />
            </div>
        );
    }
    
}




export default DecorateWork;